body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #3498db;
  padding: 20px;
}

h1 {
  margin: 0;
  font-size: 36px;
}

input[type="text"] {
  width: 100%;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
}

main {
  max-width: 800px;
  background-color: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .1);
}

.results-container {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.country {
  width: calc(33.33% - 20px);
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.country h2 {
  margin-top: 0;
  font-size: 18px;
}

.country p {
  margin: 5px 0;
  font-size: 14px;
}

.country img {
  max-width: 100px;
  height: auto;
}

@media screen and (max-width: 768px) {
  .results-container {
    flex-direction: column;
  }

  .country {
    width: 100%;
  }
}

